.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
  z-index: 10;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  position: relative;
  padding: 20px;
  border-radius: 12px;
  background-color: white;
  transform: scale(0.5);
  transition: 0.4s all;
  max-width: 70vw;
}

#dark .modal__content {
  background-color: #2a3035;
  color: #d1eaff;
}

.modal__content.active {
  transform: scale(1);
}

.close__button {
  display: inline-block;
  position: absolute;
  font-size: 20px;
  top: 0;
  right: 12px;
  padding: 4;
  cursor: pointer;
}
